/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
button,
input,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: modernNo20;
  src: url(./assets/fonts/modernNo20.ttf);
}
@font-face {
  font-family: corbel;
  src: url(./assets/fonts/corbel.ttf);
}
:root {
  display: inline-block;
  min-height: 100%;
  min-width: 100%;
}

body {
  display: inline-block;
  min-height: 100%;
  min-width: 100%;
}
#root {
  display: inline-block;
  min-height: 100%;
  min-width: 100%;
  font-family: "corbel";
}

@media screen and (min-width: 1800px) {
  :root {
    font-size: 16px;
  }
}
@media screen and (max-width: 1700px) {
  :root {
    font-size: 12px;
  }
}
@media screen and (max-width: 1300px) {
  :root {
    font-size: 10px;
  }
}
